import React from 'react';
import Header from './Header';
import styled from "styled-components";
import theme from "../style/theme";
import OnboardingText from "../assets/onboarding.png";
import AfterText from "../assets/after_complete.png";
import One from "../assets/circle_first.png";
import Two from "../assets/circle_second.png";
import Three from "../assets/circle_third.png";

const Wrapper = styled.main`
  background: ${theme.colors.white}
  background-size: cover;
`;

const Section = styled.section`
  padding: 0 16px;
`;

const OnboardingTitleWrapper = styled.div`
`;

const OnboardingTitle = styled.h2`
  width: 209px;
  height: 23px;
  background: url(${OnboardingText}) no-repeat center;
  background-size: contain;
  text-indent: -9999px;
	margin-bottom: 11px;
`;

const OnboardingDetail = styled.h3`
	font-size: 16px;
	font-weight: 500;
	letter-spacing: -0.5px;
	line-height: 25.6px;
`;

const CompleteTitle = styled.h2`
width: 222px;
height: 23px;
background: url(${AfterText}) no-repeat center;
background-size: contain;
text-indent: -9999px;
margin-bottom: 11px;
`;

const FirstDesc = styled.p`
display: flex;
flex-flow: row nowrap;
align-items: center;
	font-size: 16px;
	letter-spacing: -.4px;
	line-height: 25.6px;

	&::before{
		display: inline-block;
		content: "";
		width: 24px;
		height: 24px;
		background: url(${One}) no-repeat center;
		background-size: contain;
		margin-right: 8px;
	}
`;

const SecondDesc = styled.p`
display: flex;
flex-flow: row nowrap;
align-items: center;
	font-size: 16px;
	letter-spacing: -.4px;
	line-height: 25.6px;
	&::before{
		display: inline-block;
		content: "";
		width: 24px;
		height: 24px;
		background: url(${Two}) no-repeat center;
		background-size: contain;
		margin-right: 8px;
	}
`;

const ThirdDesc = styled.p`
display: flex;
flex-flow: row nowrap;
align-items: top;
	font-size: 16px;
	letter-spacing: -.4px;
	line-height: 25.6px;
	&::before{
		display: inline-block;
		content: "";
		width: 24px;
		height: 24px;
		background: url(${Three}) no-repeat center;
		background-size: contain;
		margin-right: 8px;
	}
`;


const OnBoarding  = () => {

	return (<Wrapper>
      <Header />
			<Section style={{	marginTop: '59px'}}>
			<OnboardingTitleWrapper>
				<OnboardingTitle>   </OnboardingTitle>
				<OnboardingDetail>   , <b style={{fontSize: '16px'}}></b>  <br/>   .</OnboardingDetail>
			</OnboardingTitleWrapper>
			</Section>
			<Section>
				<ul>
					<li>
						<img src=""/>
						<FirstDesc>   !</FirstDesc>					
					</li>
					<li>
					<img src=""/>
						<SecondDesc>   .</SecondDesc>					
					</li>
				</ul>
			</Section>
			<Section>
				<CompleteTitle>  </CompleteTitle>
				<img src="" />
				<ThirdDesc>  <br/>   !</ThirdDesc>
			</Section>
	</Wrapper>
	);
}

export default OnBoarding;